// >> this file is for the colos that gonna be used in the project:

@use "../helpers" as *;

/* --- Start Project's colors --- */

:root {

/* Light colors */
  --light-clr: #fff;
  --white-clr: #fff;
  --cultured-clr: #F0F4F5;
  --sonic-silver: #F0F4F5;
  --umber-clr: #F0F4F5;

/* Colorful colors */
  --denim-clr: #0059B2;
  --primary-clr: #FF9122;
  --mantis-clr: #25D366;
  --royal-purple-clr: #7555B7;
  --primary-light-clr: #FFB973;
  --primary-dark-clr: #FFB973;

  /* Dark Colors */
  --black-clr: #000;
  --davys-grey-clr: #757575;
  --eerie-black-clr: #1C2124;
  --eerie-black-clr-1: #161A1D;
  --independence-clr: #535362;
  --transparent-black-clr: #0000001a;
  --transparent-white-clr: #ffffff3b;
}

/* prefer color scheme */

// Dark theme 
@media (prefers-color-scheme: dark) {
  :root {
    --white-clr: #292b2e;
    --umber-clr: #6d6054;
    --sonic-silver: #737373;
    --independence-clr: #e3796a;
    --primary-dark-clr: #AA825A;
    --davys-grey-clr: #F0F4F5;
    --denim-clr: #082D53;
    --mantis-clr: #537c42;
    --royal-purple-clr: #382958;
  }

  img.dark {
    filter: brightness(75%);
  }

  .landing.home {
    background-image: url("../images/mobile-landing-dark.png") !important;
