    }
  }
  .landing.markatescil {
    background-image: url("../images/markatescilbg2-dark.png") !important;
    @include breakpoint(laptop) {
      background-image: url("../images/markatescilbg-dark.png") !important;
    }
  }
  .landing.sanaltur {
